.mywork {
  padding-top: 15rem;
  background-image: linear-gradient(to bottom, white, $color-rose);

  &-title {
    text-align: center;
    text-transform: uppercase;
    text-shadow: .5rem 1rem 2rem rgba(black, .4);
    color: $color-turquoise-dark;
    font-size: 2.7rem;
    letter-spacing: 10px;
    font-weight: 400;
    padding-top: 6rem;
    padding-bottom: 2rem;

    @include respond(phone) {
      font-size: 2rem;
    }
  }

  &-subtitle {
    text-align: center;
    color: $color-turquoise;
    font-style: italic;
  }

  &-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    @include respond(tab-port) {
      grid-template-columns: 1fr;
    }

    @include respond(phone) {
      grid-template-columns: 1fr;
  }

    &-project {
      grid-column-start: auto;
      display: grid;
      column-gap: 3rem;

      &-category {
        position: relative;
        border-radius: 0.2rem;
        justify-self: center;
        display: grid;
        color: $color-rose-dark;
        border: 1px solid;
        border-top: transparent;
        border-right: transparent;
        border-left: transparent;
        border-bottom-color: $color-rose-dark;
        text-shadow: .5rem 1rem 2rem rgba(black, .4);
        padding: 2rem 2rem 2rem 2rem;
        margin: 3rem 10rem 4.5rem 10rem;
        font-size: 3rem;
        font-weight: 200;
        letter-spacing: 8px;

        @include respond(phone) {
          font-size: 2rem;
          margin: 3rem;
          font-weight: 300;
        }

        @include respond(tab-port) {
          margin: 3rem;
        }

        &:hover {
          background-color: $color-rose;
        }
      }

        &-picture {
          position: relative;
          max-width: 45rem;
          height: 28rem;
          justify-self: center;
          box-shadow: 0 2.5rem 4rem rgba($color-rose-dark, .5);
          outline-offset: 2rem;

          &:hover {
              outline: 1.3rem solid $color-rose-dark;
              transform: scale(1.05);
              box-shadow: 0 2.5rem 4rem rgba(black, .5);
              z-index: 20;
              transition-duration: 1.2s;
            }

          @include respond(phone) {
            max-width: 34rem;
            height: 16rem;
          }

          @include respond(tab-land) {
            width: 45rem;
            height: 25rem;
          }
        }
    }
  }

  &-imgText {
    text-align: center;
    padding-top: 4rem;
    padding-bottom: 3rem;
    font-style: italic;
    color: $color-rose-darkerT;

    &-1 {
      //color: $color-turquoise-dark;
      text-shadow: 0.7rem 1rem 2rem rgba(black, .6);
      font-weight: 400;
      font-size: 1.4rem;
    }

    &-2 {
      text-shadow: 0.7rem 1rem 2rem rgba(black, .6);
      font-weight: 400;
      font-size: 1.3rem;
    }
  }
}